@import url(//db.onlinewebfonts.com/c/c00548f5d217ed324ae9096051927001?family=STARWARS);
body {
  margin: 0;
  background: url(/BuyYourNewSpaceship/static/media/deep-space.0b5dd05f.jpg) no-repeat center center fixed;
  background-size: cover;
  font-family: Arial;
}
#card {
	background-color: rgba(117, 116, 116, 0.6);
	display: flex;
	width: 30rem;
	height: 15rem;
	border-radius: 10px;
	align-items: center;
	margin: 20px;
}

img {
	width: 13rem;
	height: 13rem;
}

@media only screen and (max-width: 600px) {
	img {
		width: 8rem;
		height: 8rem;
	}
}

ul {
	list-style: none;
}

h2 {
	font-size: 3.7vh;
}

h3 {
	font-size: 3.3vh;
}

h4 {
	font-size: 2.8vh;
}

h5 {
	font-size: 2.3vh;
}
#container {
	display: flex;
	flex-wrap: wrap;
	flex-direction: row;
	width: 100%;
	justify-content: center;
}
@font-face {
	font-family: "STARWARS"; 
	src: url("//db.onlinewebfonts.com/t/c00548f5d217ed324ae9096051927001.eot"); 
	src: url("//db.onlinewebfonts.com/t/c00548f5d217ed324ae9096051927001.eot?#iefix") format("embedded-opentype"), url("//db.onlinewebfonts.com/t/c00548f5d217ed324ae9096051927001.woff2") format("woff2"), url("//db.onlinewebfonts.com/t/c00548f5d217ed324ae9096051927001.woff") format("woff"), url("//db.onlinewebfonts.com/t/c00548f5d217ed324ae9096051927001.ttf") format("truetype"), url("//db.onlinewebfonts.com/t/c00548f5d217ed324ae9096051927001.svg#STARWARS") format("svg");
}

h1 {
	font-family: "STARWARS";
	color: white;
	letter-spacing: 2px;
}
